<template>
    <v-form class="fill-height" v-model="isValid">
        <v-row class="fill-height" justify="center" align="center">
                <v-card class="pa-5" width="800px">
                    <div class="title pb-3">
                        Create Room
                    </div>
                    <v-row>
                        <v-col cols="12">
                            <v-text-field
                                v-model="name"
                                :rules="nameRules"
                                :counter="25"
                                label="Name"
                                required
                                outlined
                                dense
                                autofocus
                            />
                        </v-col>

                        <v-col cols="12">
                            <v-select
                                v-model="participants"
                                :items="otherAuthors"
                                :rules="participantsRules"
                                item-text="firstName"
                                label="Participants"
                                return-object
                                multiple
                                required
                                outlined
                                dense
                            /> 
                        </v-col>

                        <v-col cols="12">
                            <v-row no-gutters justify="space-between">
                                <v-btn
                                    class="red--text"
                                    depressed
                                    outlined
                                    to="/messages"
                                    exact
                                >
                                    Cancel
                                </v-btn>
                                <v-btn
                                    class="grey--text text--darken-2"
                                    depressed
                                    outlined
                                    @click="() => validateAndCreateRoom()"
                                >
                                    Create
                                </v-btn>
                            </v-row>
                        </v-col>
                    </v-row>
                </v-card>
        </v-row>
    </v-form>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
    name: 'CreateRoomView',

    data: () => ({
        isValid: false,

        name: '',
        nameRules: [
            v => !!v || 'Name is required',
            v => v.length <= 25 || 'Name must be less than 25 characters',
        ],

        participants: [],
        participantsRules: [
            v => v.length > 0 || 'You must include at least one participant'
        ],
    }),

    computed: {
        ...mapGetters({
            userId: 'auth/userId',
            authors: 'messaging/authors',
        }),

        otherAuthors(){
            return this.authors.filter(author => author._id !== this.userId)
        },

        currentAuthor(){
            return this.authors.filter(author => author._id === this.userId)[0]
        },
    },

    created(){
        this.loadData()
    },

    beforeDestroy(){
        this.clearAuthors
    },

    methods: {
        ...mapActions({
            clearAuthors: 'messaging/clearAuthors',
            getAuthors: 'messaging/getAuthors',
            createRoom: 'messaging/createRoom',
            joinRoom: 'messaging/joinRoom',
        }),

        resetValues(){
            this.name = ''
            this.participants = []
        },

        async loadData(){
            await this.getAuthors()
        },

        async validateAndCreateRoom(){
            if (this.isValid){

                console.log(this.participants)
                const newRoom = await this.createRoom({
                    name: this.name,
                    authors: [ ...this.participants, this.currentAuthor ],
                })

                this.joinRoom(newRoom._id)

                this.resetValues()

                this.$router.push({ name: 'room', params: { id: newRoom._id } })
            }
        },
    },
}
</script>

