const auth = {
    namespaced: true,
    state: {
        // Using fake users until users is connected
        userId: process.env.VUE_APP_USER_ID,
        // userId: '79bdc04b-100c-4513-9549-c0de35a02ff3',
        // userId: '70992af8-81e9-4315-86f9-98a219698bf0',
        error: {}
    },
    mutations: {
        request(state){
            state.status = 'loading'
        },
        success(state, user){
            state.status = 'success'
            state.userId = user.id
        },
        error(state, err){
            state.userId = ''
            state.error = err
        },
        logout(state){
            state.userId = ''
        }
    },
    actions: {
        login({ commit }, user){
            return new Promise((resolve) => {
                commit('request')
                this.authService.loginWithRedirect()
                commit('success', this.authService.user)
                resolve()
            })
        },
        logout({ commit }){
            return new Promise((resolve) => {
                commit('logout')
                this.authService.logout()
                resolve()
            })
        }
    },
    getters: {
        userId: state => state.userId,
        isAuthenticated: () => this.authService.isAuthenticated,
        error: state => state.error
    }
}
export default auth