<template>
    <div class="grey lighten-3 fill-height">
        <messages-nav />
        <v-main class="fill-height">
            <router-view />
        </v-main>
    </div>
</template>

<script>
import MessagesNav from './MessagesNav.vue'
import { mapActions, mapGetters } from 'vuex'

export default {
    name: 'DashLayout',

    components: {
        'messages-nav': MessagesNav,
    },

    computed: {
        ...mapGetters({
            userId: 'auth/userId',
        }),
    },

    created(){
        this.joinRoom(this.userId)
    },

    beforeDestroy(){
        this.leaveAllRooms()
    },

    methods: {
        ...mapActions({
            joinRoom: 'messaging/joinRoom',
            leaveAllRooms: 'messaging/leaveAllRooms',
        }),
    },
}
</script>

