<template>
    <v-container class="fill-height pa-0" fluid>
        <div class="d-flex flex-column fill-height fill-width">
            <div>
                <v-toolbar flat dense>
                    <v-row class="fill-height" align="center" justify="space-between" no-gutters>
                        <div class="title">{{ room.name }}</div>
                        <div>
                            <v-btn @click="() => showOptions()" icon tile>
                                <v-icon>mdi-dots-horizontal</v-icon>
                            </v-btn>
                            <v-btn to="/messages" exact icon tile>
                                <v-icon>mdi-close</v-icon>
                            </v-btn>
                        </div>
                    </v-row>
                </v-toolbar>
                <v-divider/>
            </div>
            <div class="d-flex flex-column-reverse justify-start flex-grow-1 overflow-scroll">
                <div
                    v-for="message in messages"
                    :key="message._id"
                    :class="`d-flex flex-row justify-${isCurrentUser(message.authorId) ? 'end' : 'start'}`"
                >
                    <div class="mx-5 mt-3">
                        <div v-if="!isCurrentUser(message.authorId)" class="caption">{{ getAuthor(message.authorId).firstName }}</div>
                        <v-card :class="isCurrentUser(message.authorId) ? 'blue lighten-4' : ''" max-width="600px" flat>
                            <v-card-text class="body-1 grey--text text--darken-4">{{ message.text }}</v-card-text>
                        </v-card>
                    </div>
                </div>
            </div>
            <div class="mx-5 mb-3 mt-5">
                <v-row align="center" no-gutters>
                    <v-text-field
                        ref="text-input"
                        v-model="newMessage"
                        placeholder="Type your message here..."
                        solo
                        flat
                        hide-details
                        autofocus
                        @keydown.enter="() => sendMessage()"
                    />
                    <v-btn
                        class="grey--text text--darken-2 ml-5"
                        x-large
                        icon
                        @click="() => sendMessage()"
                    >
                        <v-icon>mdi-send</v-icon>
                    </v-btn>
                </v-row>
            </div>
        </div>
    </v-container>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
    name: 'RoomView',

    data(){
        return {
            newMessage: '',
        }
    },

    computed: {
        ...mapGetters({
            userId: 'auth/userId',
            room: 'messaging/room',
        }),

        authors(){
            if (!this.room.authors) return []

            return this.room.authors
        },
        
        messages(){
            if (!this.room.messages) return []

            return this.room.messages
        },
    },

    watch: {
        '$route.params.id': function(){ 
            this.resetValues()
            this.loadData()
            requestAnimationFrame(() => {
                return this.$refs['text-input'].focus()
            })
        }
    },

    created(){
        this.loadData()
    },

    beforeDestroy(){
        this.clearRoom()
    },
    
    methods: {
        ...mapActions({
            clearRoom: 'messaging/clearRoom',
            getRoom: 'messaging/getRoom',
            getMessages: 'messaging/getMessages',
            createMessage: 'messaging/createMessage',
        }),

        resetValues(){
            this.newMessage = ''
        },

        async loadData(){
            const { id } = this.$route.params

            await this.getRoom(id)
            await this.getMessages({ roomId: id })
        },

        isCurrentUser(authorId){
            return authorId === this.userId
        },

        getAuthor(authorId){
            return this.authors.filter(author => author._id === authorId)[0] || {}
        },
        
        showOptions(){
            console.log('clicked')
        },

        async sendMessage(){
            this.newMessage = this.newMessage.trim()
            if (this.newMessage.length > 0){
                await this.createMessage({
                    roomId: this.$route.params.id,
                    authorId: this.userId,
                    text: this.newMessage,
                })
            }
            this.newMessage = ''
        }
    },
}
</script>

