import api from '../'

const authorApi = {
    async createAuthor(creationArgs){
        const { _id, firstName, lastName } = creationArgs

        const uri = 'api/v1/authors'
        const body = { _id, firstName, lastName }

        const { data: { author } } = await api.request('post', uri)

        return author
    },

    async getAllAuthors(filters = {}){
        const { includeArchived, createdBefore, limit = 0 } = filters

        const queryParams = []

        if (includeArchived !== undefined){
            queryParams.push(`includeArchived=${includeArchived}`)
        }

        if (createdBefore !== undefined){
            queryParams.push(`createdBefore=${createdBefore}`)
        }

        if (limit !== undefined){
            queryParams.push(`limit=${limit}`)
        }

        const uri = `api/v1/authors${!!queryParams.length ? '?' + queryParams.join('&') : ''}`

        const { data: { authors } } = await api.request('get', uri)

        return authors
    },

    async getAuthorById(id){
        const uri = `api/v1/authors/${id}`

        const { data: { author } } = await api.request('get', uri)

        return author
    },

    async updateAuthorById(id, updateArgs){
        const socket = this
        const { firstName, lastName } = updateArgs

        const message = await new Promise((resolve, reject) => {
            socket.emit('UPDATE_AUTHOR', { id, updateValues: { firstName, lastName } }, 
                ({ message, error }) => {
                    if (error) reject(error)

                    resolve(message)
                })
        })
        
        return message
    },

    async archiveAuthorById(id){
        const socket = this

        const message = await new Promise((resolve, reject) => {
            socket.emit('ARCHIVE_AUTHOR', { id }, ({ message, error }) => {
                if (error) reject(error)

                resolve(message)
            })
        })
        
        return message
    },

    async deleteAuthorById(id){
        const socket = this

        const message = await new Promise((resolve, reject) => {
            socket.emit('DELETE_AUTHOR', { id }, ({ message, error }) => {
                if (error) reject(error)

                resolve(message)
            })
        })
        
        return message
    },
}

export default authorApi