import api from '../'

const messageApi = {
    async createMessage(creationArgs){
        const socket = this
        const { roomId, authorId, text } = creationArgs

        const message = await new Promise((resolve, reject) => {
            socket.emit('CREATE_MESSAGE', { roomId, authorId, text }, ({ message, error }) => {
                if (error) reject(error)

                resolve(message)
            })
        })
        
        return message
    },

    async getAllMessages(filters = {}){
        const { roomId, authorId, includeArchived, createdBefore, limit = 0 } = filters

        const queryParams = []

        if (roomId !== undefined){
            queryParams.push(`roomId=${roomId}`)
        }

        if (authorId !== undefined){
            queryParams.push(`authorId=${authorId}`)
        }

        if (includeArchived !== undefined){
            queryParams.push(`includeArchived=${includeArchived}`)
        }

        if (createdBefore !== undefined){
            queryParams.push(`createdBefore=${createdBefore}`)
        }

        if (limit !== undefined){
            queryParams.push(`limit=${limit}`)
        }

        const uri = `api/v1/messages${!!queryParams.length ? '?' + queryParams.join('&') : ''}`

        const { data: { messages } } = await api.request('get', uri)

        return messages
    },

    async getMessageById(id){
        const uri = `api/v1/messages/${id}`

        const { data: { message } } = await api.request('get', uri)

        return message
    },

    async updateMessageById(id, updateArgs){
        const socket = this
        const { text } = updateArgs

        const message = await new Promise((resolve, reject) => {
            socket.emit('UPDATE_MESSAGE', { id, updateValues: { text } }, ({ message, error }) => {
                if (error) reject(error)

                resolve(message)
            })
        })
        
        return message
    },

    async archiveMessageById(id){
        const socket = this

        const message = await new Promise((resolve, reject) => {
            socket.emit('ARCHIVE_MESSAGE', { id }, ({ message, error }) => {
                if (error) reject(error)

                resolve(message)
            })
        })
        
        return message
    },

    async deleteMessageById(id){
        const socket = this

        const message = await new Promise((resolve, reject) => {
            socket.emit('DELETE_MESSAGE', { id }, ({ message, error }) => {
                if (error) reject(error)

                resolve(message)
            })
        })
        
        return message
    },
}

export default messageApi