import Vue from 'vue'
import authorApi from '../../api/messaging/authors'
import roomApi from '../../api/messaging/rooms'
import messageApi from '../../api/messaging/messages'

// To be removed
import messagingApi from '../../api/messaging'

const namespaced = true

const state = {
    isConnected: false,
    connectedRooms: [],
    authors: [],
    rooms: [],
    room: {},
    message: {},

    // To be removed
    messages: [], 
}

const getters = {
    isConnected(state){
        return state.isConnected
    },

    authors(state){
        return state.authors
    },
    
    rooms(state){
        return state.rooms
    },
    
    room(state){
        return state.room
    },

    message(state){
        return state.message
    },

    // To be removed
    messages(state){
        return state.messages
    }
}

const actions = {
    // Resets

    clearAuthors({ commit }){
        commit('clearAuthors')
    },

    clearRooms({ commit }){
        commit('clearRooms')
    },

    clearRoom({ commit }){
        commit('clearRoom')
    },

    clearMessage({ commit }){
        commit('clearMessage')
    },

    // Incoming system events

    SOCKET_CONNECT({ commit }){
        commit('setIsConnected', true)
    },

    SOCKET_DISCONNECT({ commit }){
        commit('setIsConnected', false)
    },

    // Rooms outgoing requests

    async createRoom({ commit }, creationArgs){
        const room = await roomApi.createRoom.bind(this._vm.$socket)(creationArgs)
        
        commit('addNewRoom', { ...room, mostRecentMessage: {} })
        commit('setRoom', room)

        return room
    },

    joinRoom({ commit }, room){
        roomApi.joinRoom.bind(this._vm.$socket)(room)
    },

    joinRooms({ commit }, rooms){
        roomApi.joinRooms.bind(this._vm.$socket)(rooms)
    },

    leaveRoom({ commit }, room){
        roomApi.leaveRoom.bind(this._vm.$socket)(room)
    },

    leaveRooms({ commit }, rooms){
        roomApi.leaveRooms.bind(this._vm.$socket)(rooms)
    },

    leaveAllRooms({ state, commit }){
        roomApi.leaveRooms.bind(this._vm.$socket)(state.connectedRooms)
    },

    async getRooms({ state, commit }, filters){
        const rooms = await roomApi.getAllRooms(filters)

        if (state.rooms.length > 0){
            commit('addRooms', rooms)
        } else {
            commit('setRooms', rooms)
        }
    },

    async getRoom({ commit }, id){
        const room = await roomApi.getRoomById(id)

        commit('setRoom', room)
    },

    async updateRoom({ commit }, { id, updateArgs }){
        const room = await roomApi.updateRoomById.bind(this._vm.$socket)(id, updateArgs)

        commit('removeAndAddUpdatedRoom', { id, newRoom: room })
        commit('updateRoom', room)
    },

    async archiveRoom({ commit }, id){
        const room = await roomApi.archiveRoomById.bind(this._vm.$socket)(id)

        commit('removeAndAddUpdatedRoom', { id, newRoom: room })
        commit('clearRoom')
    },

    async deleteRoom({ commit }, id){
        const room = await roomApi.deleteRoomById.bind(this._vm.$socket)(id)

        commit('removeRoom', id)
        commit('clearRoom')
    },

    // Rooms incoming responses

    SOCKET_ROOM_CREATED({ commit }, { room }){
        commit('addNewRoom', { ...room, mostRecentMessage: {} })
    },

    SOCKET_ROOM_JOINED({ commit }, room){
        commit('addConnectedRoom', room)
    },

    SOCKET_ROOMS_JOINED({ commit }, rooms){
        commit('addConnectedRooms', rooms)
    },

    SOCKET_ROOM_LEFT({ commit }, room){
        commit('removeConnectedRoom', room)
    },

    SOCKET_ROOMS_LEFT({ commit }, rooms){
        commit('removeConnectedRooms', rooms)
    },

    SOCKET_ROOM_UPDATED({ state, commit }, { room }){
        commit('removeAndAddUpdatedRoom', { id, newRoom: room })

        if (state.room._id === room._id){
            commit('updateRoom', room)
        }
    },

    SOCKET_ROOM_ARCHIVED({ commit }, { room }){
        commit('removeAndAddUpdatedRoom', { id, newRoom: room })

        if (state.room._id === room._id){
            commit('clearRoom')
        }
    },

    SOCKET_ROOM_DELETED({ commit }, { room }){
        commit('removeRoom', id )

        if (state.room._id === room._id){
            commit('clearRoom')
        }
    },
    
    // Messages outgoing requests

    async createMessage({ commit }, creationArgs){
        const message = await messageApi.createMessage.bind(this._vm.$socket)(creationArgs)

        commit('addNewMessage', message)
        commit('setMostRecentMessage', { id: message.roomId, message })
    },

    async getMessages({ commit }, filters){
        const messages = await messageApi.getAllMessages(filters)

        if (!state.room.messages){
            commit('setMessages', messages)
        } else {
            commit('addMessages', messages)
        }
    },

    async getMessage({ commit }, id){
        const message = await messageApi.getMessageById(id)

        commit('setMessage', message)
    },

    async updateMessage({ commit }, { id, updateArgs }){
        const message = await messageApi.updateMessageById.bind(this._vm.$socket)(id, updateArgs)

        commit('replaceMessage', { id, newMessage: message })
        commit('updateMessage', message)
        commit('setMostRecentMessage', { id: message.roomId, message })
    },

    async archiveMessage({ commit }, id){
        const message = await messageApi.archiveMessageById.bind(this._vm.$socket)(id)

        commit('replaceMessage', { id, newMessage: message })
        commit('clearMessage')
    },

    async deleteMessage({ commit }, id){
        const message = await messageApi.deleteMessageById.bind(this._vm.$socket)(id)

        commit('removeMessage', id)
        commit('clearMessage')
    },

    // Messages incoming responses

    // To be used in new implementation
    SOCKET_MESSAGE_CREATED({ state, commit }, { message }){
        commit('setMostRecentMessage', { id: message.roomId, message})

        if (state.room._id === message.roomId){
            commit('addNewMessage', message)
        }
    },

    SOCKET_MESSAGE_UPDATED({ state, commit }, { message }){
        commit('replaceMostRecentMessage', { id: message.roomId, message })

        if (state.room._id === message.roomId){
            commit('replaceMessage', { id: message.roomId, newMessage: message })
        }

        if (state.message._id === message._id){
            commit('updateMessage', message)
        }
    },

    SOCKET_MESSAGE_ARCHIVED({ commit }, { message }){
        commit('replaceMostRecentMessage', { id: message.roomId, message })

        if (state.room._id === message.roomId){
            commit('replaceMessage', { id: message.roomId, newMessage: message })
        }

        if (state.message._id === message._id){
            commit('clearMessage')
        }
    },

    SOCKET_MESSAGE_DELETED({ commit }, { message }){
        commit('removeMostRecentMessage', message.roomId)

        if (state.room._id === message.roomId){
            commit('removeMessage', message.roomId)
        }

        if (state.message._id === message._id){
            commit('clearMessage')
        }
    },

    // Authors outgoing requests

    async getAuthors({ commit }, filters){
        const authors = await authorApi.getAllAuthors(filters)

        if (state.authors.length > 0){
            commit('addAuthors', authors)
        } else {
            commit('setAuthors', authors)
        }
    },
    
    // Authors incoming responses

    SOCKET_AUTHOR_UPDATED({ state, commit }, { author }){
        commit('replaceAuthor', author)
    },

    SOCKET_AUTHOR_ARCHIVED({ commit }, { author }){
        commit('archiveAuthor', author._id)
    },

    SOCKET_AUTHOR_DELETED({ commit }, { author }){
        commit('removeAuthor', author._id)
    },

    // To be removed
    CREATE_MESSAGE(context, messageObj){
        messagingApi.createMessage(this._vm.$socket, messageObj)
    },
}

const mutations = {
    // System event mutators

    setIsConnected(state, isConnected){
        state.isConnected = isConnected
    },

    // Rooms mutators 
    
    clearRooms(state){
        state.rooms = []
    },

    setRooms(state, rooms){
        state.rooms = rooms
    },

    addRooms(state, rooms){
        state.rooms.push(...rooms)
    },

    addNewRoom(state, room){
        state.rooms.unshift(room)
    },

    removeAndAddUpdatedRoom(state, { id, newRoom }){
        let oldRoom = {}

        state.rooms = state.rooms.filter(room => {
            const shouldReplace = room.id === id

            if (shouldReplace) oldRoom = room
            
            return !shouldReplace
        })

        state.rooms.unshift({ ...oldRoom, ...newRoom })
    },

    removeRoom(state, id){
        state.rooms = state.rooms.filter(room => room.id !== id)
    },

    // Rooms Message mutators

    setMostRecentMessage(state, { id, message }){
        state.rooms = state.rooms.map(room => {
            return room._id === id
                ? { ...room, mostRecentMessage: message }
                : room
        })
    },

    replaceMostRecentMessage(state, { id, message }){
        state.rooms = state.rooms.map(room => {
            return room._id === id
                ? { ...room, mostRecentMessage: { ...room.mostRecentMessage, ...message } }
                : room
        })
    },

    removeMostRecentMessage(state, id){
        state.rooms = state.rooms.map(room => {
            return room._id === id
                ? { ...room, mostRecentMessage: {} }
                : room
        })
    },

    // Room mutators

    clearRoom(state){
        state.room = {}
    },

    setRoom(state, room){
        state.room = room
    },

    updateRoom(state, room){
        state.room = { ...state.room, ...room }
    },

    // Connected Rooms mutators 
    addConnectedRoom(state, joinedRoom){
        state.connectedRooms.push(joinedRoom)
    },

    addConnectedRooms(state, joinedRooms){
        state.connectedRooms.push(...joinedRooms)
    },

    removeConnectedRoom(state, leftRoom){
        state.connectedRooms = state.connectedRooms.filter(room => room !== leftRoom )
    },

    removeConnectedRooms(state, leftRooms){
        state.connectedRooms = state.connectedRooms.filter(room => !leftRooms.includes(room))
    },

    // Room Messages mutators

    setMessages(state, messages){
        Vue.set(state.room, 'messages', messages)
    },

    addMessages(state, messages){
        if (!state.room.messages){
            Vue.set(state.room, 'messages', [])
        }

        state.room.messages.push(...messages)
    },

    addNewMessage(state, message){
        if (!state.room.messages){
            Vue.set(state.room, 'messages', [])
        }

        state.room.messages.unshift(message)
    },

    replaceMessage(state, { id, newMessage }){
        if (!state.room.messages){
            Vue.set(state.room, 'messages', [])
            state.room.messages.push(newMessage)
        } else {
            state.room.messages = state.room.messages.map(message => {
                return message._id === id
                    ? { ...message, ...newMessage }
                    : message
            })
        }
    },

    removeMessage(state, id){
        if (!state.room.messages){
            Vue.set(state.room, 'messages', [])
        } else {
            state.room.messages = state.room.messages.filter(message => message.id !== id)
        }
    },

    // Message mutators

    clearMessage(state){
        state.message = {}
    },

    setMessage(state, message){
        state.message = message
    },

    updateMessage(state, message){
        state.message = { ...state.message, ...message }
    },

    // Author mutators

    clearAuthors(state){
        state.authors = []
    },

    setAuthors(state, authors){
        state.authors = authors
    },

    addAuthors(state, authors){
        state.authors.push(...authors)
    },

    replaceAuthor(state, newAuthor){
        state.rooms = state.rooms.map(room => {
            const authors = room.authors.map(author => {
                return author._id === newAuthor._id
                    ? { ...author, ...newAuthor }
                    : author
            })

            return { ...room, authors }
        })

        state.room = {
            ...state.room,
            authors: state.room.authors.map(author => {
                return author._id === newAuthor._id
                    ? { ...author, ...newAuthor }
                    : author
            })
        }
    },

    archiveAuthor(state, id){
        state.rooms = state.rooms.map(room => {
            const authors = room.authors.map(author => {
                return author._id === id
                    ? { ...author, isArchived: true, isActive: false }
                    : author
            })

            return { ...room, authors }
        })

        state.room = {
            ...state.room,
            authors: state.room.authors.map(author => {
                return author._id === newAuthor._id
                    ? { ...author, isArchived: true, isActive: false }
                    : author
            })
        }
    },

    removeAuthor(state, id){
        state.rooms = state.rooms.map(room => {
            const authors = room.authors.filter(author => author._id !== id)

            return { ...room, authors }
        })

        state.room = {
            ...state.room,
            authors: state.room.authors.filter(author => author._id !== id)
        }
    },

    // To be removed
    SOCKET_MESSAGE_CREATED(state, data){
        state.messages.push(data)
    },
}

export default {
    namespaced,
    state,
    getters,
    actions,
    mutations,
}