<template>
    <v-navigation-drawer app dark>
        <v-row class="fill-height" no-gutters>
            <v-col cols="12" align-self="start">
                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title class="font-weight-medium">
                            Global Software Solutions
                        </v-list-item-title>
                    </v-list-item-content>
                </v-list-item>

                <v-divider></v-divider>

                <v-list
                    dense
                    nav
                >
                    <v-list-item
                        v-for="item in items"
                        :key="item.title"
                        :to="item.link"
                    >
                        <v-list-item-content>
                            <v-list-item-title>{{ item.title }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
            </v-col>

            <v-col cols="12" align-self="end">
                <v-list
                    dense
                    nav
                >
                    <!-- Logout -->
                    <v-list-item
                        v-if="!$auth.loading && $auth.isAuthenticated"
                        @click="logout"
                    >
                        <v-list-item-content>
                            <v-list-item-title>Log Out</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
            </v-col>
        </v-row>
    </v-navigation-drawer>
</template>

<script>
import { mapActions } from 'vuex'

export default {
    name: 'AppNav',
    data(){
        return {
            items: [
                { title: 'Dashboard', link: '/dashboard' },
                { title: 'Messages', link: '/messages' },
            ]
        }
    },
    methods: {
        ...mapActions({
            authlogout: 'auth/logout',
        }),
        logout(){
            
            this.authlogout()
            
        }
    }
}
</script>

<style scoped>
</style>