import api from '../'

const roomApi = {
    async createRoom(creationArgs){
        const socket = this
        const { name, authors } = creationArgs

        const room = await new Promise((resolve, reject) => {
            socket.emit('CREATE_ROOM', { name, authors }, ({ room, error }) => {
                if (error) reject(error)

                resolve(room)
            })
        })
        
        return room
    },

    joinRoom(room){
        const socket = this

        socket.emit('JOIN_ROOM', room)
    },
    
    joinRooms(rooms){
        const socket = this

        socket.emit('JOIN_ROOMS', rooms)
    },

    leaveRoom(room){
        const socket = this

        socket.emit('LEAVE_ROOM', room)
    },

    leaveRooms(rooms){
        const socket = this

        socket.emit('LEAVE_ROOMS', rooms)
    },
    
    async getAllRooms(filters = {}){
        const { authorId, onlyActive, includeArchived, updatedBefore, limit = 0 } = filters

        const queryParams = []

        if (authorId !== undefined){
            queryParams.push(`authorId=${authorId}`)
        }

        if (onlyActive !== undefined){
            queryParams.push(`onlyActive=${onlyActive}`)
        }

        if (includeArchived !== undefined){
            queryParams.push(`includeArchived=${includeArchived}`)
        }

        if (updatedBefore !== undefined){
            queryParams.push(`updatedBefore=${updatedBefore}`)
        }

        if (limit !== undefined){
            queryParams.push(`limit=${limit}`)
        }

        const uri = `api/v1/rooms${!!queryParams.length ? '?' + queryParams.join('&') : ''}`

        const { data: { rooms } } = await api.request('get', uri)

        return rooms
    },

    async getRoomById(id){
        const uri = `api/v1/rooms/${id}`

        const { data: { room } } = await api.request('get', uri)

        return room
    },

    async updateRoomById(id, updateArgs){
        const socket = this
        const { name, authors } = updateArgs
        
        const room = await new Promise((resolve, reject) => {
            socket.emit('UPDATE_ROOM', { id, updateValues: { name, authors } },
                ({ room, error }) => {
                    if (error) reject(error)

                    resolve(room)
                })
        })
        
        return room
    },

    async archiveRoomById(id){
        const socket = this

        const room = await new Promise((resolve, reject) => {
            socket.emit('ARCHIVE_ROOM', { id }, ({ room, error }) => {
                if (error) reject(error)

                resolve(room)
            })
        })
        
        return room
    },

    async deleteRoomById(id){
        const socket = this

        const room = await new Promise((resolve, reject) => {
            socket.emit('DELETE_ROOM', { id }, ({ room, error }) => {
                if (error) reject(error)

                resolve(room)
            })
        })
        
        return room
    },
}

export default roomApi