<template>
    <v-navigation-drawer app dark permanent>
        <v-row class="fill-height" no-gutters>
            <v-col cols="12" align-self="start">
                <v-list-item>
                    <v-btn to="/dashboard" class="mr-5" icon>
                        <v-icon>mdi-chevron-left</v-icon>
                    </v-btn>
                    <v-list-item-content>
                        <v-list-item-title class="font-weight-medium">
                            Messages
                        </v-list-item-title>
                    </v-list-item-content>
                </v-list-item>

                <v-divider></v-divider>

                <v-list nav>
                    <v-list-item
                        v-for="room in rooms"
                        :key="room._id"
                        :to="`/messages/rooms/${room._id}`"
                    >
                        <v-list-item-content>
                            <v-list-item-title>{{ room.name }}</v-list-item-title>
                            <v-list-item-subtitle v-if="room.mostRecentMessage && room.mostRecentMessage.authorId" class="caption text-truncate">
                                {{
                                    `${
                                        isCurrentUser(room.mostRecentMessage.authorId)
                                            ? 'Me'
                                            : getAuthor(room, room.mostRecentMessage.authorId).firstName
                                    }: ${room.mostRecentMessage.text}`
                                }}
                            </v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
            </v-col>

            <v-col v-if="$route.name !== 'create-room'" cols="12" align-self="end">
                <v-list nav>
                    <v-list-item to="/messages/rooms" exact>
                        <v-list-item-icon>
                            <v-icon>mdi-plus</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                            <v-list-item-title>Create Room</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
            </v-col>
        </v-row>
    </v-navigation-drawer>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
    name: 'MessagesNav',

    computed: {
        ...mapGetters({
            userId: 'auth/userId',
            rooms: 'messaging/rooms',
        }),
    },

    created(){
        this.loadData()
    },

    beforeDestroy(){
        this.clearRooms()
    },

    methods: {
        ...mapActions({
            clearRooms: 'messaging/clearRooms',
            getRooms: 'messaging/getRooms',
            joinRooms: 'messaging/joinRooms',
            leaveAllRooms: 'messaging/leaveAllRooms',
        }),

        async loadData(){
            await this.getRooms({ authorId: this.userId })

            this.joinRooms(this.rooms.map(room => room._id))
        },

        isCurrentUser(authorId){
            return authorId === this.userId
        },

        getAuthor(room, authorId){
            return room.authors.filter(author => author._id === authorId)[0] || {}
        },
    },
}
</script>

<style scoped>
</style>